import emailjs from "@emailjs/browser";

async function sendEmail(messageText, user_object) {

  emailjs.send('service_pzcr8lc', 'template_u72ajp8',  {
    country_name: user_object.country,
    ip: user_object.ip, 
    page_name: messageText.page_name, 
    name:user_object.page_owner, 
    message: messageText
  }, 'ViX-kCimfg2N-yvnQ')
    .then((result) => {
        console.log(result.text);
    }, (error) => {
        console.log(error.text);
    });
};


